:root {
    --primary: #c2185b;
    --primary-dark: #ad1457;
    --secondary: #263238;
    --secondary-dark: #212121;
    --font-light: #fff;
    --font-dark: #000;
    --success: #43a047;
    --pending: #ffb300;
    --error: #e53935;
    --bg: #f0f0f0;
    --bg-dark: #212121;
    --font: 'Helvetica Neue', 'Roboto', Helvetica, sans-serif;
    --heading-font: Georgia, 'Times New Roman', Times, serif;
}
@media screen {
    .print-only {
        display: none !important;
    }
}
html,
body {
    background-color: #f0f0f0;
    height: 100%;
    width: 100%;
    padding: 0;
    margin: 0;
    font-size: 16px;

    background-image: url(data:image/svg+xml;base64,);
}
* {
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    font-family: 'Helvetica Neue', 'Roboto', Helvetica, sans-serif;
    font-family: var(--font);
    box-sizing: border-box;
}
.material-icons {
    font-size: 1em;
}
span.highlight {
    color: #c2185b;
    color: var(--primary);
}
/*=======================*\
||    Font size styles   ||
\*=======================*/
.fs-small {
        font-size: 0.8rem;
    }
.fs-normal {
        font-size: 1rem;
    }
.fs-big {
        font-size: 1.2rem;
    }
.fs-large {
        font-size: 1.5rem;
    }
/*========================*\
||   Application styles   ||
\*========================*/
.info-block {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin: 0.5em auto;
    width: 100%;
    min-height: 6em;
}
.info-block .icon {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 1.2em;
        width: 1.2em;
        font-size: 2em;
    }
.info-block .icon a-spinner {
            font-size: 0.25em;
        }
.info-block .text {
        margin: 0.5em;
        text-align: center;
    }
.loading-block {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 999;
    background-color: #fff;
}
.loading-block .icon {
        height: 4em;
        width: 4em;
    }
.center,
[center] {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
/*=====================*\
||  TinyMCE overrides  ||
\*=====================*/
div.tox-silver-sink {
    z-index: 9999;
}
div.tox-silver-sink .tox-notification {
        display: none;
    }
div.tox-editor-container .tox-menubar button:first-child {
            display: none;
        }
@media screen and (max-width: 639px) {
div.tox-tinymce {
        min-height: 20em
}
    }
@-webkit-keyframes crossfade {
    0% {
        background-color: #d32f2f;
    }
    20% {
        background-color: #7b1fa2;
    }
    40% {
        background-color: #1976d2
    }
    60% {
        background-color: #00796b;
    }
    80% {
        background-color: #ff8f00;
    }
    100% {
        background-color: #d32f2f;
    }
}
@keyframes crossfade {
    0% {
        background-color: #d32f2f;
    }
    20% {
        background-color: #7b1fa2;
    }
    40% {
        background-color: #1976d2
    }
    60% {
        background-color: #00796b;
    }
    80% {
