
input,
textarea {
    -webkit-user-select: text;
       -moz-user-select: text;
        -ms-user-select: text;
            user-select: text;
    background: none;
}

label {
    width: 100%;
    font-size: .875rem;
    margin-bottom: .5rem;
    min-width: 100%;
    font-weight: bold;
    margin-bottom: .5rem;
}

label > span:first-child {
        color: var(--pending);
    }

label.error {
        color: var(--error);
    }
